<template>
  <div>
    <h4 class="mb-1">Current User Statistics - USD</h4>

    <div class="rounded my-shadow table-50-vh">
      <b-table-simple
        no-border-collapse
        sticky-header
        bordered
        responsive
        striped
        class="rounded mb-0 th-fixed-height"
        small
      >
        <b-thead>
          <b-tr>
            <b-th style="min-width: 250px">Name</b-th>
            <b-th style="min-width: 250px">Group</b-th>
            <b-th>Current Credit</b-th>
            <b-th>Current Equity</b-th>
            <b-th>Withdrawable</b-th>
            <b-th>P/L Today</b-th>
            <b-th>%Change Equity</b-th>
            <b-th>P/L Yesterday</b-th>
            <b-th>P/L This Month</b-th>
            <b-th>P/L Last Month</b-th>
            <b-th>P/L Overall</b-th>
            <b-th>#Lots This Month</b-th>
            <b-th>%Profitable Closed Trades</b-th>
            <b-th>AVG Profit per Trade This Month</b-th>
            <b-th>%Profitable Total Trades</b-th>
            <b-th>AVG Floating P/L per Open Trade</b-th>
          </b-tr>
        </b-thead>
        <b-tbody class="text-right">
          <b-tr>
            <b-td class="text-center bg-black" colspan="2">Total</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.CurrentCredit, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.CurrentEquity, 0)}}</b-td>
            <b-td :class="dataTotal.Withdrawable >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.Withdrawable, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.PLToday, 0)}}</b-td>
            <b-td :class="dataTotal['%ChangeEquity'] >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal['%ChangeEquity'], 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.PLYesterday, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.PLThisMonth, 0)}}</b-td>
            <b-td :class="dataTotal.PLLastMonth >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.PLLastMonth, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.PLOverall, 0)}}</b-td>
            <b-td :class="dataTotal['#LotsThisMonth'] >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal['#LotsThisMonth'], 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal['%ProfitableClosedTrade'], 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal['AVGProfitPerTradeThisMonth'], 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal['%ProfitableTotalTrade'], 0)}}</b-td>
            <b-td :class="dataTotal.AVGFloatingPLPerOpenTrade >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.AVGFloatingPLPerOpenTrade, 0)}}</b-td>
          </b-tr>
          <b-tr v-for="(item, index) in current_user_statistics" :key="index" class="text-right">
            <b-td class="text-left">{{ item.properties.server }}-{{ item.properties.login }}</b-td>
            <b-td class="text-left">{{ item.properties.server }}-{{ item.properties.group }}</b-td>
            <b-td>{{ formatMoney(item.value.CurrentCredit, 0) }}</b-td>
            <b-td>{{ formatMoney(item.value.CurrentEquity, 0) }}</b-td>
            <b-td :class="item.value.Withdrawable >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.Withdrawable, 0)}}</b-td>
            <b-td>{{ formatMoney(item.value.PLToday, 0) }}</b-td>
            <b-td :class="item.value.Withdrawable >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value["%ChangeEquity"], 0)}}</b-td>
            <b-td>{{ formatMoney(item.value.PLYesterday, 0) }}</b-td>
            <b-td>{{ formatMoney(item.value.PLThisMonth, 0) }}</b-td>
            <b-td :class="item.value.PLLastMonth >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.PLLastMonth, 0)}}</b-td>
            <b-td>{{ formatMoney(item.value.PLOverrall, 0) }}</b-td>
            <b-td :class="item.value['#LotsThisMonth'] >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value["#LotsThisMonth"], 0)}}</b-td>
            <b-td>{{formatMoney(item.value["%ProfitableClosedTrade"], 0)}}</b-td>
            <b-td>{{formatMoney(item.value.AVGProfitPerTradeThisMonth, 0)}}</b-td>
            <b-td>{{formatMoney(item.value["%ProfitableTotalTrade"], 0)}}</b-td>
            <b-td :class="item.value.AVGFloatingPLPerOpenTrade >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.AVGFloatingPLPerOpenTrade, 0)}}</b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>

      <div class="py-1 pr-1 d-flex justify-content-between">
        <div>
          <b-button v-for="(level, index) in ['Company', 'Region', 'Team']" :key="index"
            size="sm" class="my-shadow ml-1 button-active"
            v-ripple.400="'rgba(60, 60, 60, 0.6)'"
            variant="outline-dark" pill
          >
            {{level}}
          </b-button>

          <b-button
            size="sm" class="my-shadow ml-1"
            v-ripple.400="'rgba(60, 60, 60, 0.6)'"
            variant="outline-dark" pill
          >
            Client
          </b-button>

          <b-button
            size="sm" class="my-shadow ml-1"
            v-ripple.400="'rgba(60, 60, 60, 0.6)'"
            variant="outline-dark" pill
          >
            Account
          </b-button>
        </div>

        <div class="my-auto"> Accounts: {{current_user_statistics.length}}</div>

      </div>
    </div>
  </div>
</template>
<script>
import {
  BTableSimple,
  BThead,
  BTr,
  BTh,
  BTd,
  BTbody,
  BTfoot,
  BButton,
} from "bootstrap-vue";
import Ripple from "vue-ripple-directive";
import axios from "@axios";
import { formatMoney } from '@core/utils/filter'


export default {
  components: {
    BTableSimple,
    BThead,
    BTr,
    BTh,
    BTd,
    BTbody,
    BTfoot,
    BButton,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      current_user_statistics: [],
    };
  },

  computed: {
    dataTotal(){
      let label = ['CurrentCredit', 'CurrentEquity', 'Withdrawable', 'PLToday', '%ChangeEquity', 'PLYesterday', 'PLThisMonth', 'PLLastMonth', 'PLOverall', '#LotsThisMonth', '%ProfitableClosedTrade', 'AVGProfitPerTradeThisMonth', '%ProfitableTotalTrade', 'AVGFloatingPLPerOpenTrade']
      let result = {}
      label.map( field => result[field] = 0)
      this.current_user_statistics.map(item => {
        label.map( field => {
          if(item.value[field]) result[field] += item.value[field]
        })
      })
      return result
    }
  },
  
  
  methods: {
    formatMoney
  },
  mounted() {
    axios.get(`${process.env.VUE_APP_API_URL}metric/CurrentUserStatistics/data`).then(res => {
      this.current_user_statistics = res.data
    })
  },
};
</script>