<template>
  <div>
    <h4 class="mb-1">Today User Statistics - USD</h4>
    <div class="rounded my-shadow table-60-vh">
      <b-table-simple
        no-border-collapse
        sticky-header
        bordered
        responsive
        striped
        small
        class="rounded mb-0 th-fixed-height"
      >
        <b-thead>
          <b-tr>
            <b-th style="min-width: 250px">Name</b-th>
            <b-th style="min-width: 250px">Group</b-th>
            <b-th>Closed Lots</b-th>
            <b-th>Traded Volume</b-th>
            <b-th>Closed P/L</b-th>
            <b-th>Swap on Closed P/L</b-th>
            <b-th>Total Closed P/L</b-th>
            <b-th>Open Lots</b-th>
            <b-th>Open Volume</b-th>
            <b-th>Floating P/L</b-th>
            <b-th>Swap on Floating P/L</b-th>
            <b-th>Total on Floating P/L</b-th>
            <b-th>Actual P/L</b-th>
            <b-th>Net Deposists</b-th>
            <b-th>Net Credits</b-th>
            <b-th>Closed P/L Yesterday</b-th>
          </b-tr>
        </b-thead>
        <b-tbody class="text-right">
          <b-tr>
            <b-td class="text-center bg-black" colspan="2">Total</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.ClosedLots, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.TradedVolume, 0)}}</b-td>
            <b-td :class="dataTotal.ClosedPL >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.ClosedPL, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.SwapOnClosedPL, 0)}}</b-td>
            <b-td :class="dataTotal.TotalClosedPL >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.ClosedLots, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.OpenLots, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.OpenVolume, 0)}}</b-td>
            <b-td :class="dataTotal.FloatingPL >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.FloatingPL, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.SwapOnFloatingPL, 0)}}</b-td>
            <b-td :class="dataTotal.TotalOnFloatingPL >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.TotalOnFloatingPL, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.ActualPL, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.NetDeposits, 0)}}</b-td>
            <b-td class="bg-black">{{formatMoney(dataTotal.NetCredits, 0)}}</b-td>
            <b-td :class="dataTotal.ClosedPLYesterday >= 0 ? 'bg-black text-success' : 'bg-black text-danger'">{{formatMoney(dataTotal.ClosedPLYesterday, 0)}}</b-td>
          </b-tr>
          <b-tr v-for="(item, index) in today_user_statistics" :key="index">
            <b-td class="text-left">{{ item.properties.server }}-{{ item.properties.login }}</b-td>
            <b-td class="text-left">{{ item.properties.server }}-{{ item.properties.group }}</b-td>
            <b-td>{{ formatMoney(item.value.ClosedLots, 0) }}</b-td>
            <b-td>{{ formatMoney(item.value.TradedVolume, 0) }}</b-td>
            <b-td :class="item.value.ClosedPL >= 0 ? 'text-success' : 'text-danger'">{{ formatMoney(item.value.ClosedPL, 0)}}</b-td>
            <b-td>{{ formatMoney(item.value.SwapOnClosedPL, 0) }}</b-td>
            <b-td :class="item.value.TotalClosedPL >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.TotalClosedPL, 0)}}</b-td>
            <b-td>{{ formatMoney(item.value.OpenLots, 0) }}</b-td>
            <b-td>{{ formatMoney(item.value.OpenVolume, 0) }}</b-td>
            <b-td :class="item.value.FloatingPL >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.FloatingPL, 0)}}</b-td>
            <b-td>{{ formatMoney(item.value.SwapOnFloatingPL, 0) }}</b-td>
            <b-td :class="item.value.TotalFloatingPL >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.TotalFloatingPL, 0)}}</b-td>
            <b-td>{{ formatMoney(item.value.ActualPL, 0) }}</b-td>
            <b-td>{{ formatMoney(item.value.NetDeposits, 0) }}</b-td>
            <b-td>{{ formatMoney(item.value.NetCredits, 0) }}</b-td>
            <b-td :class="item.value.ClosedPLYesterday >= 0 ? 'text-success' : 'text-danger'">{{formatMoney(item.value.ClosedPLYesterday, 0)}}</b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>

      <div class="py-1 pr-1 d-flex justify-content-between">
        <div>
          <b-button v-for="(level, index) in ['Company', 'Region', 'Team']" :key="index"
            size="sm" class="my-shadow ml-1 button-active"
            v-ripple.400="'rgba(60, 60, 60, 0.6)'"
            variant="outline-dark" pill
          >
            {{level}}
          </b-button>

          <b-button
            size="sm" class="my-shadow ml-1"
            v-ripple.400="'rgba(60, 60, 60, 0.6)'"
            variant="outline-dark" pill
          >
            Client
          </b-button>

          <b-button
            size="sm" class="my-shadow ml-1"
            v-ripple.400="'rgba(60, 60, 60, 0.6)'"
            variant="outline-dark" pill
          >
            Account
          </b-button>
        </div>

        <div class="my-auto"> Accounts: {{today_user_statistics.length}}</div>
      </div>
      
    </div>
  </div>
</template>
<script>
import {
  BTableSimple,
  BThead,
  BTr,
  BTh,
  BTd,
  BTbody,
  BTfoot,
  BButton,
} from "bootstrap-vue";
import Ripple from "vue-ripple-directive";
import axios from "@axios";
import { formatMoney } from '@core/utils/filter'


export default {
  components: {
    BTableSimple,
    BThead,
    BTr,
    BTh,
    BTd,
    BTbody,
    BTfoot,
    BButton,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      today_user_statistics: [],
    };
  },

  computed: {
    dataTotal(){
      let label = ['ClosedLots', 'TradedVolume', 'ClosedPL', 'SwapOnClosedPL', 'TotalClosedPL', 'OpenLots', 'FloatingPL', 'SwapOnFloatingPL', 'TotalOnFloatingPL', 'ActualPL', 'NetDeposits', 'NetCredits', 'ClosedPLYesterday']
      let result = {}
      label.map( field => result[field] = 0)
      this.today_user_statistics.map(item => {
        label.map( field => {
          if(item.value[field]) result[field] += item.value[field]
        })
      })
      return result
    }
  },


  methods: {
    formatMoney,
  },
  mounted() {
    axios
      .get(`${process.env.VUE_APP_API_URL}metric/TodayUserStatistics/data`)
      .then((res) => {
        this.today_user_statistics = res.data;
      });
  },
};
</script>